<template>
  <div id="ranking">
      <div class="music-container">
          <!-- 侧栏 -->
          <div class="aside">
              <!-- 特色榜 -->
              <div class="rank" >
                  <h3>特色榜</h3>
                  <div class="item" v-for="(item, index) in rankList" :key="index" @click="goplayListDetail(item.id)">
                      <img v-lazy="item.coverImgUrl" alt="">
                  </div>
              </div>
          </div>
          <!-- 主体 -->
          <div class="content">
              <!-- 媒体榜 -->
               <div class="rank">
                 <h3>媒体榜</h3>
                 <div class="rank-content">
                   <div class="item" v-for="(item, index) in otherList" :key="index" @click="goplayListDetail(item.id)">
                      <img v-lazy="item.coverImgUrl" alt="">
                   </div>
                 </div>
               </div>
          </div>
      </div>
  </div>
</template>
<script>
import { req_All_topList } from "../network/song/index.js";
export default {
  name: "Ranking",
  data() {
    return {
      rankList: [],
      otherList: [],
    };
  },
  created() {
    this.getTopSong();
  },
  methods: {
    // 获取排行版
    getTopSong() {
      req_All_topList(this).then(res => {
        // 特色榜
        this.rankList = res.list.slice(0, 4);
        // 媒体版
        this.otherList = res.list.slice(4, res.list.length);
      });
    },
     // 歌单页面
    goplayListDetail(id) {
      this.$router.push({
        path: `/playListDetail/${new Date().getTime()}`,
        query: { id },
      });
    },
  },
};
</script>
<style lang="scss">
@import "../assets/scss/music.scss";
#ranking {
  .music-container {
    width: 1000px;
    @include flex(space-between, flex-start);
    .aside {
      width: 160px;
      .rank {
          margin-top: 30px;
          @include flex();
          flex-direction: column;
          h3 {
             @include font(24px, 60px, $colorC);
             margin-left: -78px;
          }
          .item {
              width: 160px;
              margin-right: 20px;
              margin-bottom: 20px;
              border-right: 1px solid rgba($color: #000000, $alpha: 0.3);
              img {
              display: block;
              width: 100px;
              height: 100px;
            }
          }
      }
    } 
    .content {
      flex: 1;
      padding-left: 20px;
      box-sizing: border-box;
      .rank {
         margin-top: 30px;
        h3 {
           @include font(24px, 60px, $colorC);
        }
        .rank-content {
        
           @include flex();
           flex-wrap: wrap;
           .item {
              width: 100px;
              margin-right: 20px;
              margin-bottom: 20px;
              img {
                display: block;
                width: 100px;
                height: 100px;
              }
           }
        }
      }
    }
  }
}
</style>